<template lang='pug'>
.container
  .container-box
    el-form(ref="form" :model="form" label-width="80px")
      el-form-item(label="账号")
        el-input(v-model="form.account")
      el-form-item(label="密码")
        el-input(v-model="form.password" type="password")
      el-form-item
        el-button(type="primary" @click="login") 登录
</template>
<script>
import common from '@/common';
export default {
name: 'Login',
 data() {
 return {
     form:{
      account:"",
      password:""
     }
};
},
methods: {
    login(){
        let vm=this;
        let params = this.form;
        common.fetchData({
          method: 'get',
          url: CONSTANT.url.login,
          params: params,
          success(data) {
            console.log(data)
            // vm.$message({message: data.mes,type: 'success',duration:2000});
            // setTimeout(()=>{
            //     localStorage.setItem('user_info', JSON.stringify(data));
            //     common.emitUserInfo(data);
            //     vm.$router.push({name:"Index"});
            // },1000)
          },
          error(data){
             console.log(data)
          }

        });
    }
},
components: {}
}
</script>
<style scoped lang='stylus'>
  .container
    height 500px
    .container-box
        width 30%
        margin 50px auto 0
        border 1px solid #ccc
        padding 50px
</style>



